import { jsOptions } from "../../.fable/fable-library.3.1.14/Util.js";
import { replace, interpolate, toConsole } from "../../.fable/fable-library.3.1.14/String.js";

const Dom_domRefs = new Map([]);

window.fluke = Dom_domRefs;

export function Dom_set(key, value) {
    Dom_domRefs.set(key, value);
}

export function Dom_newObj(fn) {
    return jsOptions(fn);
}

export function Dom_resetZoom() {
    const viewport = window.document.querySelector("meta[name=viewport]");
    const initialContent = viewport.content;
    toConsole(interpolate("initial=%P()\n        devicePixelRatio=%P()\n        availWidth/clientWidth=%P()\n        ", [initialContent, window.devicePixelRatio, window.screen.availWidth / document.documentElement.clientWidth]));
    viewport.content = replace(initialContent, "initial-scale=1, maximum-scale=10", "user-scalable=no");
    void setTimeout(() => {
        viewport.content = initialContent;
    }, 1000);
}

